@keyframes banner{
    0%{
        opacity: 1;
    }
    50%{
        opacity: .9;
    }
    100%{
        opacity: 1;
    }
}

@keyframes icon{
    0%{
        opacity: 1;
        transform: scale(1);
    }
    100%{
        opacity: 0;
        transform: scale(1.4);
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}