@import 'main';
// End Of Import

// Preloader
.preloader{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-prim;
    @include Flex;
    z-index: 9999;

    .spinner{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 4px solid $color-sec;
        border-left: 4px solid #fec918;
        animation: spin 1s linear infinite;
    }
}

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba($color-sec, 0.8);
    box-shadow: 0 0 10px rgba($color-sec, .6);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 99;
    display: none;

    html[dir="ltr"] & {
        left: 30px;
        right: unset;
    }

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.95);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -261px;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    html[dir="ltr"] & {
        right: unset;
        left: -261px;
    }

    &.open{
        right: 0;

        html[dir="ltr"] & {
            right: unset;
            left: 0;
        }
    }

    .options {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 14px;
            color: #50332d;
        }

        .close1 {
            display: flex;
            font-size: 18px;
            color: #dedede;
            cursor: pointer;
        }

        .searBtn{
            // margin-left: auto;
            // margin-right: 20px;
            margin-right: auto;
            margin-left: 20px;
            display: flex;
            align-items: center;
            
            html[dir="ltr"] & {
                margin-left: auto;
                margin-right: 20px;
            }

            i{
                font-size: 16px;
                color: #dedede;
            }
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 260px;
        // overflow: auto;
        padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid #dedede;
            }

            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: $color-sec;
                transition: .5s;
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

// Start Header
.head{
    padding: 30px 0 23px;
    background-color: $color-prim;

    .content{
        @include Flex;
        justify-content: space-between;

        @include Sm{
            justify-content: center;
        }

        .search{
            width: 260px;
            position: relative;

            @include Sm{
                position: fixed;
                top: 0%;
                left: 0%;
                width: 100%;
                height: 100%;
                background-color: rgba($color-sec, .98);
                z-index: 9999;
                @include Flex;
                opacity: 0;
                visibility: hidden;
                transition: .5s;

                &.open{
                    opacity: 1;
                    visibility: visible;
                }
            }

            form{
                position: relative;

                i{
                    display: none;
                    position: absolute;
                    top: -230%;
                    left: 50%;
                    transform: translateX(-50%);
                    color: $color-prim;
                    font-size: 24px;
                }

                @include Sm{
                    width: 70%;
                    top: -15%;

                    i{
                        display: block;
                    }
                }

                &:after{
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                    background-color: $color-sec;
                    transform: scaleX(0);
                    transform-origin: right;
                    transition: .5s;

                    @include Sm{
                        background-color: $color-prim;
                    }
                }

                &:focus-within{

                    &:after{
                        transform: scaleX(1);
                        transform-origin: left;
                    }
                }
            }

            input{
                width: 100%;
                border: 0;
                border-bottom: 1px solid #B88D1A;
                background-color: transparent;
                height: 40px;
                padding: 10px;
                transition: .5s;
                font-size: 14px;
                color: #b58c1c;

                &::placeholder{
                    color: #b58c1c;
                }
            }

            button{
                position: absolute;
                top: 50%;
                left: 10px;
                transform: translateY(-50%);
                background-color: transparent;
                border: 0;
                padding: 0;

                html[dir="ltr"] & {
                    right: 10px;
                    left: unset;
                }
                
                svg{

                    path{
                        transition: .5s;

                        @include Sm{
                            fill: rgba($color-prim, .6);
                        }
                    }
                }

                &:hover{

                    svg{

                        path{
                            fill: rgba($color-sec, .6);
                            
                            @include Sm{
                                fill: rgba($color-prim, 1);
                            }
                        }
                    }
                }
            }
        }

        .lang{
            width: 260px;
            @include Flex;
            justify-content: flex-end;

            @include Sm{
                display: none;
            }

            a{
                font-size: 14px;
                color: #50332d;
                transition: .5s;

                &:hover{
                    color: rgba($color-sec, .6);
                }
            }
        }
    }
}

// End OF Header

// Start Navbar

.navBar{
    background-color: #4F322C;
    padding: 14px 0;

    @include Sm{
        display: none;
    }

    ul{
        @include Flex;

        li{

            &:not(:last-child){
                margin-left: 23px;
            }

            html[dir="ltr"] &{

                &:not(:last-child){
                    margin-right: 23px;
                    margin-left: 0;
                }
            }

            a{
                color: #fff;
                font-size: 16px;
                transition: .5s;

                &:hover{
                    color: #ffcb04;
                }
            }
        }
    }
}

// End Of Navbar

// Slider

.slider{
    
    .item{
        position: relative;
        height: 590px !important;  // 800px
        overflow: hidden;

        img{
            width: 1920px !important;
            height: 590px !important;  // 800px
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    .owl-nav{
        position: absolute;
        top: 50%;
        height: 0;
        width: 100%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 9;
        @include Flex;
        justify-content: space-between;

        @media (max-width: 1200px){
            button{
                transform: none !important;
            }
        }

        @include Sm{
            display: none;
        }

        button{

            &:first-child{
                transform: translateX(17px);
            }

            &:last-child{
                transform: translateX(-17px);
            }

            &:hover{

                span{
                    background-color: #FEC918;
                }
            }


            span{
                @include Flex;
                width: 35px;
                height: 35px;
                border-radius: 50%;
                background-color: rgba($color-black, .5);
                font-size: 10px;
                color: #fff;
                transition: .5s;
            }
        }
    }

    .owl-dots{
        position: absolute;
        bottom: 60px;
        left: 0;
        z-index: 9;
        width: 100%;
        @include Flex;

        button{

            &.active{

                span{
                    background-color: #fff;
                }
            }
        }

        span{
            display: inline-block;
            width: 8px;
            height: 8px;
            background-color: rgba($color-black, .5);
            margin: 0 3px;
            border-radius: 50%;
        }

        @include Sm{
            bottom: 20px;
        }
    }
}

// Sldier

// About

.about{
    padding: 95px 0 50px;

    @include Sm{
        padding: 50px 0 30px;
    }

    h2{
        font-family: "frtBlk";
        color: #50332d;
        font-size: 28px;
        margin: 0;
        margin-bottom: 25px;
    }
    
    p{
        font-size: 18px;
        font-family: "frtLight";
        color: #616161;
        line-height: 1.667;
        margin: 0;
        margin-bottom: 40px;

        @include Sm{
            margin-bottom: 20px;
        }
    }

    a{
        font-size: 14px;
        font-family: "frtLight";
        color: #b2b2b2;
        transition: .5s;

        &:hover{
            color: $color-sec;
        }
    }
}

// About

// Banner

.banner{
    margin-bottom: 70px;

    @include Sm{
        margin-bottom: 40px;
    }

    a{
        display: block;
        overflow: hidden;
        position: relative;

        &:hover{

            & img{
                animation: banner .5s linear;
                transform: scale(1.05);
            }
        }

        img{
            transition: .5s;
        }
    }
}

// Banner

// Grid

.grid{
    padding: 80px 0 85px;
    background-color: #F9F9F9;

    @include Sm{
        padding: 40px 0 35px;

        .col-md-3{
            padding: 0;
        }
    }

    .owl-stage{
        display: flex;
        align-items: center;

    }
    
    .owl-dots{
        width: 100%;
        @include Flex;

        button{

            &.active{

                span{
                    background-color: $color-prim;
                }
            }
        }

        span{
            display: inline-block;
            width: 8px;
            height: 8px;
            background-color: rgba($color-black, .5);
            margin: 0 3px;
            border-radius: 50%;
        }
    }

    .heading{
        @include Flex;
        justify-content: space-between;
        margin-bottom: 20px;

        p{
            font-size: 24px;
            font-family: "frtBlk";
            color: #50332d;
            margin: 0;
        }

        a{
            font-size: 14px;
            font-family: "frtBold";
            color: #cacaca;
            transition: .5s;

            &:hover{
                color: $color-sec;
            }
        }
    }

    .pic{
        position: relative;
        margin-bottom: 30px;

        &:nth-child(5), &:nth-child(7){

            @include Md{
                margin-top: -46px;
            }

            @include Lg{
                margin-top: -58px;
            }
        }

        i{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            font-size: 48px;

            &:after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border: 5px solid #fff;
                border-radius: 50%;
                transition: 1s;
            }
        }

        &:hover{

            i{

                &:after{
                    animation: icon 1s linear;
                }
            }
        }
    }
}

// Grid

// News

.news{
    padding: 70px 0 100px;

    @include Sm{
        padding: 40px 0 50px;
    }

    .heading{
        @include Flex;
        justify-content: space-between;
        margin-bottom: 20px;

        p{
            font-size: 24px;
            font-family: "frtBlk";
            color: #50332d;
            margin: 0;
        }

        a{
            font-size: 14px;
            font-family: "frtBold";
            color: #cacaca;
            transition: .5s;

            &:hover{
                color: $color-sec;
            }
        }
    }

    .col-md-6{
        overflow: hidden;

        &:first-child{
            @include Sm{
                margin-bottom: 30px;
            }
        }

        a{
            display: block;
            width: 100%;
            height: 100%;
            
            .pic{
                position: relative;
                width: max-content;
                margin: auto;
                overflow: hidden;

                @include mob600{
                    width: auto;
                }
            }

            &:hover{

                .title{
                    background-color: #4F322C;
                }

                img{
                    transform: scale(1.02);
                }
            }

            img{
                transition: .5s cubic-bezier(0.77, 0, 0.175, 1);
            }
    
            .date{
                width: 50px;
                height: 50px;
                background-color: #F1B619;
                text-align: center;
                position: absolute;
                top: 10px;
                right: 10px;
                z-index: 9;
    
                html[dir="ltr"] & {
                    left: 10px;
                    right: unset;
                }

                p{
                    font-family: "frtBold";
                    font-size: 18px;
                    color: #50332d;
                    margin: 0;
                    margin-top: 10px;
                    line-height: 1;
                }

                span{
                    font-size: 12px;
                    color: #50332d;
                    display: block;
                    margin-top: -2px;
                }
            }

            .title{
                width: 100%;
                height: 55px;
                background-color: rgba(#4F322C, .8);
                @include Flex;
                justify-content: flex-start;
                padding: 30px;
                position: absolute;
                bottom: 0;
                left: 0;
                transition: .5s cubic-bezier(0.77, 0, 0.175, 1);

                p{
                    margin: 0;
                    color: #fff;
                    font-size: 16px;
                    font-family: "frtBold";
                }
            }
        }
    }
}

// News

// Footer

footer{
    padding: 30px 0;
    background-color: #4F322C;

    @include Sm{
        padding: 20px 0;
    }

    .details{
        margin-bottom: 20px;

        @include Sm{

            img{
                margin: auto;
            }
        }
    }

    .copyright{

        &.bottom{
            display: none;
        }

        @include Flex;
        justify-content: flex-start;

        @include Sm{
            justify-content: center;
            display: none;

            &.bottom{
                display: flex;
                margin-top: 15px;
            }
        }

        p,a{
            font-size: 13px;
            color: #998986;
            margin-bottom: 0;
        }

        a{
            margin-right: 5px;

            html[dir="ltr"] & {
                margin-left: 5px;
            }

            img{
                margin-right: 5px;

                html[dir="ltr"] & {
                    margin-left: 5px;
                }
            }
        }
    }

    .contact{
        margin: 15px 0;
        color: #fff;
        @include Flex;
        justify-content: flex-start;

        @include Sm{
            justify-content: center;
        }

        a{
            @include Flex;
            font-family: "frtLight";
            font-size: 18px;
            color: #fff;

            &:first-child{
                margin-left: 20px;
                
                html[dir="ltr"] & {
                    margin-left: unset;
                    margin-right: 20px;
                }
            }
        }

        i{
            margin-left: 10px;
            font-size: 16px;
            
            html[dir="ltr"] & {
                margin-left: unset;
                margin-right: 10px;
            }

            &.fa-phone{

                html[dir="ltr"] & {
                    transform: rotateY(180deg);
                }
            }
        }
    }

    .social{
        @include Flex;
        justify-content: flex-start;
        color: #fff;

        @include Sm{
            justify-content: center;
        }

        @include mob480{
            flex-wrap: wrap;
        }

        *{
            color: inherit;
        }

        p{
            font-size: 16px;
            font-family: "frtBold";
            margin: 0;
            margin-left: 12px;

            html[dir="ltr"] & {
                margin-right: 12px;
                margin-left: unset;
            }

            @include mob480{
                width: 100%;
                margin-right: 0;
                margin-left: 0;
                text-align: center;
                margin-bottom: 15px;
            }
        }

        a{
            position: relative;
            background-color: #63443D;
            border-radius: 50%;
            @include Flex;
            transition: .5s;


            &:after{
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                width: 85%;
                height: 85%;
                border-radius: 50%;
                transition: .5s;
                background-color: $color-prim;
                z-index: 1;
            }

            i{
                transition: .5s;
                position: relative;
                z-index: 2;
                font-size: 18px;
            }

            &:not(:last-child){
                margin-left: 8px;
                
                html[dir="ltr"] & {
                    margin-left: unset;
                    margin-right: 6px;
                }
            }

            &:hover{

                &:after{
                    transform: translate(-50%, -50%) scale(1);
                }

                i{
                    color: $color-sec;
                }
            }

            @include Md{
                width: 30px;
                height: 30px;

                i{
                    font-size: 16px;
                }

                &:not(:last-child){
                    margin-left: 6px;
                    
                    html[dir="ltr"] & {
                        margin-left: unset;
                        margin-right: 6px;
                    }
                }
            }

            @include Lg{
                width: 40px;
                height: 40px;

                i{
                    font-size: 18px;
                }
            }

            @include Sm{
                width: 40px;
                height: 40px;

                i{
                    font-size: 18px;
                }

                &:not(:last-child){
                    margin-left: 8px;
                    
                    html[dir="ltr"] & {
                        margin-left: unset;
                        margin-right: 8px;
                    }
                }
            }
        }
    }
}

// Footer